
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
main {
  display: flex;
  align-items: center;

  width: 100%;
  height: 100vh;
  background-color: black;

  position: relative;

  //animation-iteration-count: infinite;
  .item {
    display: inline-block;
    width: 100px;
    height: 100px;
    border-radius: 50px;
    position: absolute;


    &-1 {
      left: 500px;
      border: 3px solid red;
      animation : animate1 9s forwards ;
      //animation-fill-mode: ;
      animation-iteration-count: infinite;
      background-color: rgba(255, 0, 0, .6);

    }
    &-2 {
      left: 500px;

      z-index: 5;
      border: 3px solid yellow;
      box-shadow: inset  2px 2px 16px yellow;
      animation : animate1 9s forwards 3s ;
      animation-iteration-count: infinite;
    }
    &-3 {
      z-index: 10;
      left: 500px;

      border: 10px solid blue;
      animation : animate1 9s forwards 5s ;
      animation-iteration-count: infinite;

    }
    &-4 {
      left: 500px;

      z-index: 15;
      border: 3px solid white;
      animation : animate1 9s forwards  7s ;
      animation-iteration-count: infinite;
    }

  }
}
@keyframes animate1 {
  .001% {
    width: 100px;
    height: 100px;
    border-radius: 50px;
    opacity: 0.9;

  }
  50% {
    width: 300px;
    height: 300px;
    border-radius: 150px;
    left: 710px;
    opacity: 1;
    //transform: rotateY(120deg);


  }
  100% {
    opacity: 0.9;
    width: 100px;
    height: 100px;
    border-radius: 50px;
    left: 500px;
    transform: rotateY(360deg);



  }
}


